import {ComponentsUtils} from './../../../common/utils/components-utils';
import {SecondsTickService} from '../services/seconds-tick.service';
import {Observable, Subject} from 'rxjs';
import {ChangeDetectionStrategy, Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges} from '@angular/core';
import {map, takeUntil} from 'rxjs/operators';

@Component({
  standalone: false,
  selector: 'app-clock',
  templateUrl: './clock.component.html',
  styleUrls: ['./clock.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ClockComponent implements OnInit, OnChanges, OnDestroy {
  @Input() timeZone = 'Africa/Accra';
  @Input() hour24 = true;
  @Input() timeStamp$: Observable<number>;

  time$: Observable<string>;
  private _onDestroy$ = new Subject<void>();

  constructor(
    private readonly secondTick: SecondsTickService) {
      this.timeStamp$ = this.secondTick.getSeconds$();
    }

  ngOnInit(): void {
    this.onTimeStamp$Changed();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if(changes.timestamp$) {
      if(!changes.timestamp$.currentValue) {
        this.timeStamp$ = this.secondTick.getSeconds$();
      }
      if(changes.timestamp$.currentValue !== changes.timestamp.previousValue) {
        this.onTimeStamp$Changed();
      }
    }
  }

  ngOnDestroy(): void {
    ComponentsUtils.onComponentDestroyed(this._onDestroy$);
  }

  private onTimeStamp$Changed() {
    this.time$ = this.timeStamp$
      .pipe(
        takeUntil(this._onDestroy$),
        map(timeStamp => {
          return new Date(timeStamp).toLocaleTimeString(undefined, {
            // hour: '2-digit', minute:'2-digit',
            timeZone: this.timeZone,
            hour12: !this.hour24,
          });
        })
      );
  }

}
